@import 'base/main';
@import 'base/mixins';
@import 'base/variables';

.shared-block-footer-wrapper{
  
  @include mobile{
    
    padding: 0;
  }
}

#index-page {

  h1 {
    font-size: 3.625rem;
    margin-bottom: 0;
  }
  
  h2 {
    font-size: 2.1875rem;
    margin: 1.25rem 0;
  }

  h3 {
    font-style: italic;
    padding: 1.25rem 0;
    // padding-top: 0.5rem; 
    // padding-bottom: 1rem;
    font-size: 1.875rem;
  }
  
  h4{
    font-size: 3rem;
    font-weight: bold;
    padding-bottom: 1.25rem;
  
    @include mobile {
      padding-bottom: 1.25rem;
    }
  }

  .shared-btn {

    height: 2.625rem;
    display: flex;
    justify-content: center;
    align-items: center; 

    
  }

  .desktop-navigation-links {
    margin-bottom: 2.5rem;
  }


  // .header-btn {

  //   @include tablet {
  //     display: none;
  //   }

  //   @include desktop {
  //     display: none;
  //   }

  // }

  .main-btn {
    width: 50%;
  }

  .event-btn {
    @include tablet {
      display: none;
    }

    @include desktop {
      display: none;
    } 
  }

  .btn-wrapper {
    margin-top: auto;
    padding-bottom: 1.875rem;
  }



  .app-header {
    background-image: url('~/dist/assets/images/main-header.jpg');
    align-items: center;
    padding-top: 1.25rem;
    margin: 0;
  }
  
}

#logo-img, .desktop-navigation-links {
  @include mobile {
    display: none;
  }
}

.main-header {
  .btn {
    color: $color-header;
    border-color: $color-header;
  }
}

.event-time {
  font-size: 1.3125rem;
  // margin-top: 5.625rem;
  display: block;
}



